

























import { Component, Vue } from 'vue-property-decorator';
import { Getter } from 'vuex-class';
import { IActivityItem } from '@/types/interface';
import banner from '../../components/Banner.vue';

@Component({
  components: {
    banner,
  },
})

export default class ActivityContent extends Vue {
  @Getter('taskById', { namespace: 'task' }) taskById!: (id: string) => IActivityItem;
  @Getter('activityNotificationById', { namespace: 'notification' }) activityNotificationById!: (id: number) => IActivityItem;

  get isTest() {
    return this.content.id === '1';
  }

  get isNotification() {
    return this.$route.query.process === 'notification';
  }

  link() {
    if (this.isNotification) this.$router.push('/notification/main');
    else this.$router.push('/activity/main');
  }

  created() {
    const {
      id,
    } = this.$route.query;
    const idInt = parseInt(id.toString(), 10);

    if (id) {
      this.content = this.isNotification ? this.activityNotificationById(idInt) : this.taskById(id.toString());
    }
  }

  content = {
    id: '1',
    title: '標題標題標題標題標題1',
    start: '2020/8/1 上午 12:00',
    end: '2020/8/31 下午 23:59',
  };
}
